
<template>
    <div class="container">
        <div class="section-left">
            <slot name="title"></slot>
        </div>
        <div class="section-right">
            <div class="slot-right">
                <slot name="right"></slot>
            </div>
            <!-- <div class="time_container">{{ dateYear }} {{ dateDay }}</div> -->
            <img @click="changeVisible = true" class="icon" src="@/assets/imgs/center.png" alt="">
        </div>
        <!-- 修改密码 -->
        <el-dialog title="修改密码" :visible.sync="changeVisible" width="30%" @close="changeDialogClosed">
            <el-form :model="changeForm" :rules="changeFormRules" ref="changeFormRef" label-width="100px">
                <el-form-item label="旧密码" prop="oldPassword">
                    <el-input v-model="changeForm.oldPassword"></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="newPassword1">
                    <el-input v-model="changeForm.newPassword1"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="newPassword2">
                    <el-input v-model="changeForm.newPassword2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="changeVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitNewPassword">确 定</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
export default {
    data() {
        return {
            changeVisible: false,
            timer: null, //实时时间
            dateDay: null,
            dateYear: null,
            changeForm: {
                oldPassword: "",
                newPassword1: "",
                newPassword2: "",
            },
            changeFormRules: {
                oldPassword: [
                    { required: true, message: "请输入旧密码", trigger: "blur" },
                ],
                newPassword1: [
                    { required: true, message: "请输入新密码", trigger: "blur" },
                ],
                newPassword2: [
                    { required: true, message: "请再一次输入新密码", trigger: "blur" },
                ],
            },
        }
    },
    methods: {
        changeDialogClosed() {
            this.$refs.changeFormRef.resetFields();
        },
        // 修改密码
        async submitNewPassword() {
            this.$refs.changeFormRef.validate(async (valid) => {
                // 验证输入
                if (!valid) return;

                if (this.changeForm.newPassword1 !== this.changeForm.newPassword2) {
                    this.$message.error("新旧密码不一致");
                    return;
                }
                // 获取当前的用户信息
                const res = await getAdmin(window.sessionStorage.getItem("id"));

                if (res.code === 0) return this.$message.error("获取账户信息失败");
                if (md5(this.changeForm.oldPassword) !== res.data.password) {
                    return this.$message.error("密码错误，请检查输入");
                }
                res.data.password = this.changeForm.newPassword1;
                // 修改密码
                const updateRes = await updateAdmin(res.data);
                if (updateRes.code === 0) return this.$message.error("修改失败");
                this.$message.success("修改成功");
                this.changeVisible = false;
            });
        },
    },
    mounted() {
        this.timer = setInterval(() => {
            const date = this.$dayjs(new Date());
            this.dateDay = date.format("HH:mm:ss");
            this.dateYear = date.format("YYYY-MM-DD");
        }, 1000);
    },
    beforeDestroy() {
        if (this.timer) {
            clearInterval(this.timer);
        }
    },

}
</script>

<style lang="less" scoped>
.container {
    display: flex;
    justify-content: space-around;
    // background-color: cyan;section
    align-items: center;
    margin-bottom: 30px;

    .section-left {
        width: 50%;
    }

    .section-right {
        width: 50%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .slot-right{
            margin-right: 100px;
        }
        .time_container{
            margin-right: 20px;
            // background: hsl(210, 13%, 60%);
            text-shadow: 0 1px 1px hsla(0, 0%, 100%, 0.8);
        }
        .icon {
            cursor: pointer;
            width: 25px;
            height: 25px;
        }
    }
}
</style>